import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
export default {
	components: {
		uniLoadMore	
	},
	data() {
		return {
			headerPosition:"fixed",
			loadingType: 'more', //加载更多状态
			goodsList: [],
			mgoodsList:[],
			pn:0,
			count:'',
			zongfen:''
		};
	},
	onShow() {
		if(uni.getStorageSync('cbe_token')){
			this.loadData();
		}else{
			uni.showToast({ title:"未登录或无权限",icon: "none"});
			var timer = setTimeout(()=>{
				uni.navigateBack({
					delta:1
				})
			},1000);
		}
	},
	//下拉刷新
	onPullDownRefresh(){
		this.loadData('refresh');
	},
	//加载更多
	onReachBottom(){
		this.loadData();
	},
	methods:{
		navto(){
			uni.navigateTo({
				url:"/pages/jifen/shixiao"
			})
		},
		//加载商品 ，带下拉刷新和上滑加载
		async loadData(type='add',loading) {
			//没有更多直接返回
			if(type === 'add'){
				if(this.loadingType === 'nomore'){
					return;
				}
				this.loadingType = 'loading';
			}else{
				this.loadingType = 'more'
			}
			let goodsLista = await this.getMyshoucang();
			let goodsList  = goodsLista.data;
			// this.count = goodsLista.count;
			this.zongfen = goodsLista.zongfen;
			if(type === 'refresh'){
				this.goodsList = [];
			}
			this.goodsList = this.goodsList.concat(goodsList);
			
			//判断是否还有下一页，有是more  没有是nomore(测试数据判断大于20就没有了)
			this.loadingType  = this.goodsList.length > this.count ? 'nomore' : 'more';
			if(type === 'refresh'){
				if(loading == 1){
					uni.hideLoading()
				}else{
					uni.stopPullDownRefresh();
				}
			}
		},
		async getMyshoucang(){
			return new Promise((resolve, reject) => {
				uni.showLoading({
					title: '请稍后'
				})
				uni.request({
					url:uni.getStorageSync('hostname') + '?action=index/member/myJifen',
					data: {pn:this.pn,token:uni.getStorageSync('cbe_token')},
					method:'POST',
					dataType:'json',
					header: {'content-type' : 'application/x-www-form-urlencoded'},
					success: (res) => {
						uni.hideLoading()
						if(res.data.code==0){
							this.pn = Number(this.pn)+Number(1);
							resolve(res.data);
						}else{
							this.loadingType = "nomore"
						}
					},
					fail: (err) => {
						reject('err')
					}
				});
			})
		},
		stopPrevent(){}
	}
}
